import { mapGetters, mapMutations } from 'vuex'
import progressBar from '@/components/Progress-bar/Progress-bar'
import {playMode} from '@/store/config'
import {shuffle} from '@/common/js/utils'
export default {
  data () {
    return {
      canPlay: false,
      currentTime: 0,
      audio: {},
      percent: 0
    }
  },
  components: {
    progressBar
  },
  computed: {
    ...mapGetters([
      'playlist',
      'fullScreen',
      'currentSong',
      'playingState',
      'currentIndex',
      'mode',
      'sequenceList'
    ])
  },
  methods: {
    minimize () {
      this.SET_FULLSCREEN(false)
    },
    maximize () {
      this.SET_FULLSCREEN(true)
    },
    togglePlayingState () {
      this.SET_PLAYINGSTATE(!this.playingState)
    },
    ready () {
      this.canPlay = true
    },
    error () {
      this.canPlay = true
    },
    end () {
      if (this.mode === playMode.loop) {
        this.$nextTick(() => {
          this.audio.currentTime = 0
          this.$refs.audio.play()
        })
      } else {
        this.$nextTick(() => {
          this.next()
        })
      }
    },
    prev () {
      if (this.canPlay) {
        let index = this.currentIndex - 1
        index < 0
          ? this.SET_CURRENTINDEX(this.playlist.length - 1)
          : this.SET_CURRENTINDEX(index)
      }
      this.canPlay = false
    },
    next () {
      if (this.canPlay) {
        let index = this.currentIndex + 1
        index === this.playlist.length
          ? this.SET_CURRENTINDEX(0)
          : this.SET_CURRENTINDEX(index)
      }
      this.canPlay = false
    },
    updateTime (event) {
      this.currentTime = event.target.currentTime
      this.percent = Math.floor(this.currentTime / this.currentSong.duration * 100)
      if (!this.audio.currentTime) {
        this.audio = event.target
      }
    },
    changeProgress (percent) {
      this.percent = percent
      let time = this.currentSong.duration
      let currentTime = time * (percent / 100)
      this.audio.currentTime = currentTime
    },
    changeMode () {
      let mode = (this.mode + 1) % 3
      this.SET_MODE(mode)
      let list = []
      if (this.mode === playMode.random) {
        list = shuffle(this.sequenceList)
      } else {
        list = this.sequenceList
      }
      this.resetCurrentIndex(list)
      this.SET_PLAYLIST(list)
    },
    resetCurrentIndex (list) {
      let index = list.findIndex((song) => {
        return song.id === this.currentSong.id
      })
      this.SET_CURRENTINDEX(index)
    },
    ...mapMutations({
      SET_FULLSCREEN: 'SET_FULLSCREEN',
      SET_PLAYINGSTATE: 'SET_PLAYINGSTATE',
      SET_CURRENTINDEX: 'SET_CURRENTINDEX',
      SET_MODE: 'SET_MODE',
      SET_PLAYLIST: 'SET_PLAYLIST'
    })
  },
  watch: {
    playingState (newval, oldval) {
      this.$nextTick(() => {
        let audio = this.$refs.audio
        this.playingState ? audio.play() : audio.pause()
      })
    },
    currentSong (newsong, oldsong) {
      this.$nextTick(() => {
        if (newsong.id !== oldsong.id) {
          this.$refs.audio.play()
          this.SET_PLAYINGSTATE(true)
        }
      })
    }
  }
}



