// 解决mini播放器在底部遮挡问题
import {mapGetters} from 'vuex'
export const playlistMixin = {
  computed: {
    ...mapGetters(['playlist'])
  },
  mounted () {
    this.handlePlaylist(this.playlist)
  },
  activated () {
    this.handlePlaylist(this.playlist)
  },
  methods: {
    handlePlaylist () {
    }
  },
  watch: {
    playlist (newval, oldval) {
      this.handlePlaylist(newval)
    }
  }
}



